import React from 'react';
import { NavLink } from 'react-router-dom'
import './header.css'


const Header = () => {
    return (
        <header>
            <nav>
                <NavLink activeClassName='active' exact to='/'>React</NavLink>
                <NavLink activeClassName='active' to='/doc'>文档</NavLink>
                <NavLink activeClassName='active' to='/tuto'>教程</NavLink>
                <NavLink activeClassName='active' to='/blog'>博客</NavLink>
                <NavLink activeClassName='active' to='/comm'>社区</NavLink>
            </nav>
        </header>
    )
}

const Blog = () => {
    return (
        <div>
            Blog
        </div>
    )
}

const Comm = () => {
    return (
        <div>
            Comm
        </div>
    )
}

const Tuto = () => {
    return (
        <div>
            Tuto
        </div>
    )
}

const Login = (props) => {
    const login = ()=>{
        localStorage.setItem('userinfo','tom')
        props.history.replace(props.location.state)
    }
    return (
        <div>
            <button onClick={login}>登录</button>
        </div>
    )
}

const Home = (props) => {
    // console.log(props);
    
    return (
        
        <div style={{textAlign:'center'}}>
            <h2>React</h2>
            <p>用于构建用户界面的 JavaScript 库</p>
            <div>
                <button onClick={
                    ()=>props.history.push('/doc')
                }>快速开始</button>
                <button onClick={
                    ()=>props.history.push('/tuto')
                }>入门教程</button>
            </div>
        </div>
    )
}

export {Home};
export {Blog};
export {Comm};
export {Login};
export {Header};
export {Tuto};
