import React from 'react'
import {NavLink, Redirect,withRouter, Switch} from 'react-router-dom'
import { RouteWithSubRoutes } from '../App';
import './doc.css';


function Core() {
    return <h1>Core</h1>
}
function Guide(){
    return <h1>Guide</h1>
}
function Api(){
    return <h1>Api</h1>
}
function Hooks(){
    return <h1>Hooks</h1>
}


const Doc = ({routes}) => {
    
    return (
        <div className='doc'>
            <div className="content">
                <Switch>
                    {routes.map((route,i) => (
                        <RouteWithSubRoutes key={i} {...route} />
                    ))}
                </Switch>
                <Redirect from='/doc' to='/doc/core' />
            </div>
            <ol>
                <li><NavLink to='/doc/core'>核心概念</NavLink></li>
                <li><NavLink to='/doc/guide'>高级指引</NavLink></li>
                <li><NavLink to='/doc/api'>API</NavLink></li>
                <li><NavLink to='/doc/hooks'>Hooks</NavLink></li>
            </ol>
            
        </div>
    )
}

export default withRouter(Doc);
export {Core};
export {Guide};
export {Api};
export {Hooks};